/**
 * @description: url key
 * @param {type}
 * @return:name||null
 * @author: 
 */

export function getUrlParam(name: string) {
  var reg = new RegExp("(^|&)" + name + "=([^&]*)(&|$)");
  var r = decodeURI(window.location.search)
    .substr(1)
    .match(reg);
  if (r != null) return r[2];
  return null;
}

/**
 * @description: 
 * @param {type} 1   2   3
 * @return: arr
 * @author: 
 */
export function sort(arr: any, type: number) {
  return arr.sort((a, b) => {
    switch (type) {
      case 1:
        return a - b;
      case 2:
        return b - a;
      case 3:
        return Math.random() - 0.5;
      default:
        return arr;
    }
  })
}
/**
 * @description: 
 * @param {type}
 * @return:newArr
 * @author: 
 */

export function deWeight(arr: Array<string>) {
  if (Array.hasOwnProperty("from")) return Array.from(new Set(arr));
  var newArr = [];
  var flag = true;
  for (var i = 0; i < arr.length; i++) {
    if (arr[i] !== arr[i]) {
      if (flag && newArr.indexOf(arr[i]) === -1) {
        newArr.push(arr[i]);
        flag = false;
      }
    } else {
      if (newArr.indexOf(arr[i]) === -1) newArr.push(arr[i]);
    }
  }
  return newArr;
}


/**
 * @description:
 * @param {type}
 * @return:newArr
 * @author: 
 */
export function diffArr(arr1, arr2) {
  return arr1.concat(arr2).filter(function (arg) {
    return !(arr1.indexOf(arg) >= 0 && arr2.indexOf(arg) >= 0);
  });
}


/**
 * @description: 
 * @param {type}
 * @return:obj
 * @author: 
 */
export function cloneObj(obj: object) {
  let ret: any;
  if (Array.isArray(obj)) {
    ret = []; // 
    for (let i = 0; i < obj.length; i++) {
      ret[i] = cloneObj(obj[i]);
    }
    return ret;
  } else if (Object.prototype.toString.call(obj) === "[object Object]") {
    ret = {}; // 
    for (let i in obj) {
      ret[i] = cloneObj(obj[i]);
    }
    return ret;
  } else {
    return obj;
  }
}

/**
 * @description: 
 * @param {type}
 * @return:string
 * @author: 
 */
export function handelMobile(value) {
  if (!value) return "";
  if (typeof value !== "string") value = value.toString();
  return value.replace(/^(\d{3})\d*(\d{4})$/, "$1****$2");
}

/**
 * @description: yyyy-MM-dd
 * @param {type}
 * @return:string
 * @author: 
 */
export function formatDate(date: any) {
  let year = date.getFullYear();
  let month = date.getMonth() + 1;
  let weekDay = date.getDate();

  if (month < 10) {
    month = "0" + month;
  }
  if (weekDay < 10) {
    weekDay = "0" + weekDay;
  }
  return year + "-" + month + "-" + weekDay;
}

/**
 * @description: yy-mm-ddn
 * @param {type}
 * @return: string
 * @author: 
 */
export function addDate(date: string, days: number) {
  let d: any = new Date(date);
  d.setDate(d.getDate() + days);
  let m = d.getMonth() + 1;
  return d.getFullYear() + "-" + m + "-" + d.getDate();
}

/**
 * @description: 
 * @param {type}
 * @return:fn
 * @author: 
 */
export const Debounce = (fn: any, t: number) => {
  let delay: number = t || 1500;
  let timer: any;
  return function () {
    let args: any = arguments;
    if (timer) {
      clearTimeout(timer);
    }
    timer = setTimeout(() => {
      timer = null;
      fn.apply(this, args);
    }, delay);
  };
};

/**
 * @description: 
 * @param {type} 
 * @return: fn
 * @author: 
 */
export const throttle = (fn: any, wait: number) => {
  var pre = Date.now();
  return function () {
    let context = this;
    let args = arguments;
    let now = Date.now();
    if (now - pre >= wait || 1000) {
      fn.apply(context, args);
      pre = Date.now();
    }
  }
}
/**
 * @description: 
 * @param {type} size 
 * @return: 
 * @author: 
 */
export const formatterSizeUnit = (size) => {

  if (size) {
    let result: any = parseInt(size);
    if (result < 1024) {
      return result + " B";
    } else if (result < 1024 * 1024) {
      return Math.ceil(result / 1024) + " KB";
    } else if (result < 1024 * 1024 * 1024) {
      return Math.ceil(result / (1024 * 1024)) + " MB";
    } else {
      return Math.ceil(result / (1024 * 1024 * 1024)) + " GB";
    }
  }
}


//
export function getNowDate() {
  const date = new Date();
  let month: string | number = date.getMonth() + 1;
  let strDate: string | number = date.getDate();
  let hours: string | number = date.getHours();
  let minutes: string | number = date.getMinutes();
  if (month <= 9) {
    month = "0" + month;
  }
  if (strDate <= 9) {
    strDate = "0" + strDate;
  }
  if (hours <= 9) {
    hours = "0" + hours;
  }
  if (minutes <= 9) {
    minutes = "0" + minutes;
  }
  return (
    date.getFullYear() +
    "-" +
    month +
    "-" +
    strDate + ' ' + hours + ':' + minutes
  );
}
//
export function getNowDateHour() {
  const date = new Date();
  return (
    date.getHours() +
    ":" +
    date.getMinutes()
  );
}
/**
 * @description: 
 * @param {type}
 * @return:newArr
 * @author: limi
 */
export function unique(arr: any, u_key: string) {
  let result = []
  result[0] = arr[0]
  arr.forEach((meta_item, i) => {
    //resultpush
    let num = 0
    result.forEach((r_item, j) => {
      if (meta_item[u_key] !== r_item[u_key]) {
        num++
      }
      if (num === result.length) {
        result.push(meta_item)
      }
    })
  })
  return result
}


// 
// [{
//   name: '',
//   prop: ''
// }]
export function createExportConfig(list: any[], ignoreList: any[] = []) {
  const reqArr = [];
  list.forEach((ele, i) => {
    if (ignoreList.includes(ele.name)) return;
    reqArr.push({
      id: i + 1,
      itemName: ele.name,
      [ele.prop]: ele.name
    })
  })
  return reqArr;
}

// /, , true, ''
export function clearObj(obj: any, empty: Boolean = false) {
  if (Array.isArray(obj)) {
    if (empty) {
      obj.length = 0;
    } else {
      obj.forEach(ele => {
        ele = '';
      })
    }
  } else {
    if (empty) {
      for (let key in obj) {
        delete obj[key];
      }
    } else {
      for (let key in obj) {
        obj[key] = '';
      }
    }
  }
}

//
export function idCardMask(str, frontLen, endLen) {
  var len = str.length - frontLen - endLen;
  var xing = '';
  for (let i = 0; i < len; i++) {
    xing += '*';
  }
  return str.substring(0, frontLen) + xing + str.substring(str.length - endLen);
}

// 
export function downloadFile(data, config = {}) {
  const aLink = document.createElement("a");
  let blob = new Blob([data], {
    type: 'application/vnd.ms-excel,application/vnd.openxmlformats-officedocument.spreadsheetml.sheet'
  })
  aLink.href = URL.createObjectURL(blob);
  aLink.download = (config as any).name;
  document.body.appendChild(aLink);
  aLink.click();
  document.body.removeChild(aLink);
}

export function getMaxZIndex() {
  let maxZ = 0;
  [...document.querySelectorAll("*")].forEach(ele => {
    const elStyle = (ele as any).style;
    const zIndex = parseInt(elStyle['z-index']);
    if (elStyle['position'] != 'static' && !isNaN(zIndex)) {
      maxZ = Math.max(maxZ, zIndex);
    }
  })
  return maxZ;
}
